import { useCallback } from 'react';
import { Agent } from '../models/Agent';
import { NamedLocation, ItemType, Proposal, ProposalType, AttackEffect, SimulationLog, ActionResponse } from '../types';
import { Zombie } from '../models/Zombie';

interface UseSimulationActionsProps {
  agents: Agent[];
  locations: NamedLocation[];
  zombies: Zombie[];
  addLog: (type: SimulationLog['type'], message: string, agentId?: number) => void;
  setZombies: React.Dispatch<React.SetStateAction<Zombie[]>>;
}

export const useSimulationActions = ({ agents, locations, zombies, addLog, setZombies }: UseSimulationActionsProps) => {

  const handleMoveAction = useCallback((agent: Agent, response: ActionResponse) => {
    const targetLocationName = response.targetLocation;
    const targetLocation = locations.find(l => l.name === targetLocationName);
    if (targetLocation) {
      agent.moveTo(targetLocation);
      addLog('action', `${agent.name}${targetLocationName}`, agent.id);
    } else {
      addLog('error', `${agent.name} ${targetLocationName} `, agent.id);
    }
  }, [locations, addLog]);

  const handleWaitAction = useCallback((agent: Agent) => {
    agent.adjustEnergy(5); // 
    addLog('action', `${agent.name}`, agent.id);
  }, [addLog]);

  const handleScavengeAction = useCallback((agent: Agent) => {
    const currentLocation = locations.find(loc => loc.name === agent.currentLocationName);
    if (currentLocation && currentLocation.resources) {
      let itemFound = false;
      for (const [item, details] of Object.entries(currentLocation.resources)) {
        if (Math.random() < details.chance) {
          const quantity = Math.floor(Math.random() * details.maxQuantity) + 1;
          agent.addItemToInventory(item as ItemType, quantity);
          addLog('action', `${agent.name}${currentLocation.name}${item}${quantity}`, agent.id);
          itemFound = true;
          break; // 11
        }
      }
      if (!itemFound) {
        addLog('action', `${agent.name}${currentLocation.name}`, agent.id);
      }
    } else {
      addLog('info', `${agent.name}`, agent.id);
    }
  }, [locations, addLog]);

  const handleGiveItemAction = useCallback((agent: Agent, response: ActionResponse) => {
    const recipientAgent = agents.find(a => a.name === response.recipientName);
    const itemName = response.itemName as ItemType;

    if (recipientAgent && itemName) {
      if (agent.hasItemInInventory(itemName)) {
        agent.removeItemFromInventory(itemName);
        recipientAgent.addItemToInventory(itemName, 1); // 1
        addLog('action', `${agent.name}${recipientAgent.name}${itemName}`, agent.id);
      } else {
        addLog('info', `${agent.name}${itemName}${recipientAgent.name}`, agent.id);
      }
    } else {
      addLog('error', `${agent.name}`, agent.id);
    }
  }, [agents, addLog]);

  const handleProposeAction = useCallback((agent: Agent, response: ActionResponse) => {
    const recipientAgent = agents.find(a => a.name === response.proposalRecipientName);
    if (recipientAgent && response.proposalType && response.proposalContent) {
      const newProposal: Proposal = {
        id: `prop-${Date.now()}-${agent.id}-${recipientAgent.id}`,
        senderId: agent.id,
        recipientId: recipientAgent.id,
        type: response.proposalType as ProposalType,
        content: response.proposalContent,
        status: 'pending',
        timestamp: Date.now(),
      };
      recipientAgent.pendingProposals.push(newProposal);
      addLog('action', `${agent.name}${recipientAgent.name}${response.proposalType}: ${response.proposalContent}`, agent.id);
    } else {
      addLog('error', `${agent.name}`, agent.id);
    }
  }, [agents, addLog]);

  const handleRespondToAction = useCallback((agent: Agent, response: ActionResponse) => {
    const proposalId = response.proposalId;
    const proposalResponse = response.proposalResponse;
    const targetProposal = agent.pendingProposals.find(p => p.id === proposalId);

    if (targetProposal && proposalResponse) {
      targetProposal.status = proposalResponse === 'accept' ? 'accepted' : 'rejected';
      // pendingProposals
      agent.pendingProposals = agent.pendingProposals.filter(p => p.id !== proposalId);

      const senderAgent = agents.find(a => a.id === targetProposal.senderId);
      if (senderAgent) {
        const responseMessage = proposalResponse === 'accept'
          ? `${agent.name}${targetProposal.type}`
          : `${agent.name}${targetProposal.type}`;
        senderAgent.receivedMessages.push({ senderId: agent.id, recipientId: senderAgent.id, content: responseMessage, timestamp: new Date().toLocaleTimeString() });
        addLog('action', `${agent.name}${senderAgent.name}${targetProposal.type}${proposalResponse === 'accept' ? '' : ''}`, agent.id);

        // 
        if (proposalResponse === 'accept') {
          if (targetProposal.type === '') {
            const targetLocationName = targetProposal.content.match(/(.*?)/)?.[1];
            const targetLocation = locations.find(loc => loc.name === targetLocationName);
            if (targetLocation) {
              agent.moveTo(targetLocation);
              senderAgent.moveTo(targetLocation);
              addLog('system', `${agent.name}${senderAgent.name}${targetLocation.name}`, agent.id);
            } else {
              addLog('error', `${targetLocationName}`, agent.id);
            }
          } else if (targetProposal.type === '') {
            const targetZombie = zombies.length > 0 ? zombies[0] : null;
            if (targetZombie) {
              agent.moveTo({x: targetZombie.x, y: targetZombie.y, name: "zombie", type: "zombie", width: 10, height: 10, resources: {}}); // 
              senderAgent.moveTo({x: targetZombie.x, y: targetZombie.y, name: "zombie", type: "zombie", width: 10, height: 10, resources: {}}); // 
              addLog('system', `${agent.name}${senderAgent.name}:  (ID: ${targetZombie.id})`, agent.id);
            } else {
              addLog('info', ``, agent.id);
            }
          } else if (targetProposal.type === '') {
            const targetLocationName = targetProposal.content.match(/(.*?)/)?.[1];
            const targetLocation = locations.find(loc => loc.name === targetLocationName);
            if (targetLocation) {
              agent.moveTo(targetLocation);
              senderAgent.moveTo(targetLocation);
              addLog('system', `${agent.name}${senderAgent.name}${targetLocation.name}`, agent.id);
            } else {
              addLog('error', `${targetLocationName}`, agent.id);
            }
          }
        }
      }
    } else {
      addLog('error', `${agent.name}`, agent.id);
    }
  }, [agents, locations, zombies, addLog, setZombies]);

  const handleAttackAction = useCallback((agent: Agent, response: ActionResponse) => {
    const targetZombie = zombies.find(z => z.id === response.targetId);
    if (targetZombie && agent.weapon) {
      const damage = agent.attack(targetZombie);
      if (damage > 0) {
        targetZombie.health -= damage;
        addLog('action', `${agent.name}(ID: ${targetZombie.id})${damage} : ${targetZombie.health}`, agent.id);
        if (targetZombie.health <= 0) {
          addLog('system', `(ID: ${targetZombie.id})`, agent.id);
          setZombies(prevZombies => prevZombies.filter(z => z.id !== targetZombie.id)); // 
        }
      } else {
        addLog('info', `${agent.name}(ID: ${targetZombie.id})`, agent.id);
      }
    } else {
      addLog('error', `${agent.name}(ID: ${response.targetId})`, agent.id);
    }
  }, [zombies, addLog, setZombies]);

  const handleSendMessageAction = useCallback((agent: Agent, response: ActionResponse) => {
    const recipient = agents.find(a => a.name === response.recipientName);
    if (recipient && response.messageContent) {
      addLog('info', `${agent.name} -> ${recipient.name}: ${response.messageContent}`, agent.id);
    } else {
      addLog('error', `${agent.name}`, agent.id);
    }
  }, [agents, addLog]);

  return {
    handleMoveAction,
    handleWaitAction,
    handleScavengeAction,
    handleGiveItemAction,
    handleProposeAction,
    handleRespondToAction,
    handleAttackAction,
    handleSendMessageAction,
  };
};
