export type AgentMood = 'neutral' | 'happy' | 'sad' | 'angry' | 'fearful' | 'surprised' | 'disgusted';

// 
export type WeaponType = '' | '' | '' | '';
export type FoodType = '' | '' | '' | '' | '';
export type ItemType = WeaponType | FoodType; // 

// 
export type ProposalType = '' | '' | '';

export interface Proposal {
  id: string;
  senderId: number;
  recipientId: number;
  type: ProposalType;
  content: string; // 
  status: 'pending' | 'accepted' | 'rejected';
  timestamp: number;
}

import { MemoryManager } from '../services/memoryManager';

export interface IAgent {
  id: number;
  name: string;
  personality: string;
  memoryManager: MemoryManager; // 
  // memoryManager is a class instance, so it should be handled separately for serialization
  goals: string[];
  currentLocationName: string;
  job: Job | null;
  money: number;
  happiness: number;
  hunger: number;
  fear: number;
  shortTermPlan: string;
  weapon: Weapon | null;
  x: number;
  y: number;
  targetX: number | null;
  targetY: number | null;
  speed: number;
  energy: number;
  mood: AgentMood;
  relationships: { [agentId: string]: number };
  receivedMessages: Message[];
  inventory: { [item in ItemType]?: number };
  targetLocationName: string | null;
  pendingProposals: Proposal[];

  // Methods from Agent class
  updateState(newState: Partial<IAgent>): void;
  moveTo(location: NamedLocation): void;
  arriveAtLocation(locationName: string): void;
  move(): void;
  changeMoney(amount: number): void;
  adjustEnergy(amount: number): void;
  adjustFear(amount: number): void;
  adjustHappiness(amount: number): void;
  adjustHunger(amount: number): void;
  attack(target: IZombie): number;
  updateMood(mood: AgentMood): void;
  updateRelationship(agentId: string, change: number): void;
  receiveMessage(message: Message): void;
  addItemToInventory(item: ItemType, quantity: number): void;
  hasItemInInventory(item: ItemType): boolean;
  removeItemFromInventory(item: ItemType): void;
  consumeItem(item: ItemType): void;
}

export interface LLMConfig {
  provider: 'huggingface' | 'openai' | 'anthropic' | 'ollama';
  model: string;
  temperature: number;
  maxTokens: number;
  apiKey?: string;
  baseUrl?: string;
}

export interface NamedLocation {
  name: string;
  x: number;
  y: number;
  type: string;
  width: number;
  height: number;
  resources?: { [item in ItemType]?: { chance: number; maxQuantity: number } }; // 
  health?: number;
  ownerAgentId?: number;
}

export interface Job {
  name: string;
  salary: number;
}

export interface Weapon {
  name: WeaponType;
  damage: number;
  range: number;
  type: 'melee' | 'ranged';
}

export interface Food {
  name: FoodType;
  hungerRecovery: number;
  happinessBonus?: number;
}

export interface Message {
  senderId: number;
  recipientId: number;
  content: string;
  timestamp: string;
}

export interface SimulationLog {
  type: 'system' | 'action' | 'info' | 'error';
  message: string;
  timestamp: string;
  agentId?: number;
}

export interface IZombie {
  id: number;
  x: number;
  y: number;
  health: number;
  targetAgentId: number | null;
  takeDamage: (amount: number) => void;
}

export type ActionType = '' | '' | '' | '' | '' | '' | '' | '';

export interface ActionResponse {
  action: ActionType;
  plan: string;
  mood: string;
  energy: number;
  targetLocation?: string;
  isMessage?: boolean;
  recipientName?: string;
  messageContent?: string;
  happiness?: number;
  hunger?: number;
  targetId?: number;
  itemName?: ItemType; // 
  proposalType?: ProposalType; // 
  proposalRecipientName?: string; // 
  proposalContent?: string; // 
  proposalId?: string; // ID
  proposalResponse?: 'accept' | 'reject'; // 
}

export class LLMServiceError extends Error {
  constructor(message: string, public originalError?: Error) {
    super(message);
    this.name = 'LLMServiceError';
  }
}

export class DecisionError extends Error {
  constructor(message: string, public originalError?: Error) {
    super(message);
    this.name = 'DecisionError';
  }
}

// SavedAgentData interface for serializable agent properties
export interface SavedAgentData {
  id: number;
  name: string;
  personality: string;
  memory: string; // Storing summarized memory as string
  goals: string[];
  currentLocationName: string;
  job: Job | null;
  money: number;
  happiness: number;
  hunger: number;
  fear: number;
  shortTermPlan: string;
  weapon: Weapon | null;
  x: number;
  y: number;
  targetX: number | null;
  targetY: number | null;
  speed: number;
  energy: number;
  mood: AgentMood;
  relationships: { [agentId: string]: number };
  receivedMessages: Message[];
  inventory: { [item in ItemType]?: number };
  targetLocationName: string | null;
  pendingProposals: Proposal[];
}

export interface SavedSimulationState {
  agents: SavedAgentData[]; // Use SavedAgentData for serialization
  zombies: any[]; // Add zombies to saved state
  currentStep: number;
  llmProvider: LLMConfig['provider'];
}

export interface ActionHistory {
  action: string;
  timestamp: string;
  step: number;
  context: string;
}

export interface AttackEffect {
  id: string;
  type: 'bullet' | 'slash';
  startX: number;
  startY: number;
  endX: number;
  endY: number;
  timestamp: number;
}
